				"html": "<p>If you'll recall from our discussion of Storing Values with the Equal Operator, everything to the right of the equal sign is resolved before the value is assigned. This means we can take the return value of a function and assign it to a variable.</p>\n<p>Assume we have pre-defined a function <code>sum</code> which adds two numbers together, then:</p>\n<pre><code>var ourSum = sum(5, 12);\n</code></pre>\n<p>will call <code>sum</code> function, which returns a value of <code>17</code> and assigns it to <code>ourSum</code> variable.</p>",
